/* Modal window */
body.wpo-modal-is-opened {
	overflow: hidden;
}

.wpo-modal--container {
	position: fixed;
    top: 0;
	left: 0;
	padding: 3em;
    width: 100%;
	height: 100%;
	box-sizing: border-box;
	z-index: 9999;
	overflow: auto;
}

.wpo-modal {
	position: relative;
    background: #FFF;
	width: calc(100% - 6em);
	top: 50%;
	transform: translateY(-50%);
	margin: 0 auto;
    padding: 2em;
    box-sizing: border-box;
	z-index: 2;
	> .wpo-modal--close {
		border: none;
		background: none;
		position: absolute;
		top: 10px;
		right: 10px;
		width: 20px;
		height: 20px;
		line-height: 1;
		padding: 0;
	}
}

.wpo-modal--bg {
	position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
	background: rgba(0, 0, 0, 0.74);
	z-index: 1;
}

.wpo-confirm .wpo-modal--content > h3 {
    font-size: 2em;
}

.wpo-confirm .wpo-modal--content > h3 .dashicons {
    font-size: 30px;
    width: 30px;
    height: 30px;
    transform: translateY(-5px);
}
.wpo-confirm .wpo-modal--content > h3 .dashicons.dashicons-warning {
	color: #feb901;
}
