/* Unused images / wpo_smush_image / image grid*/

.wpo_unused_image, .wpo_smush_image {
	position: relative;
	margin: 4px;
	width: calc(50% - 8px);
	text-align: center;

	@media (min-width: 500px) {
		width: calc(25% - 8px);
	}

	@media (min-width: $breakpoint-small) {
		width: calc(16.6666% - 8px);
	}

	@media (min-width: 1100px) {
		width: calc(12.5% - 8px);
	}

	.wpo_unused_image__input {
		position: absolute;
		top: 10px;
		opacity: 0;
		width: 0;
		height: 0;
	}
	
	label {
		display: block;
		width: 100%;
		position: relative;
		padding: 1px;
		border: 3px solid $wp-lighter-gray;
		box-sizing: border-box;

		&::before {
			content: "";
			display: block;
			padding-top: 100%;
		}

		.thumbnail {
			position: absolute;
			top: 1px;
			left: 1px;
			width: calc(100% - 2px);
			height: calc(100% - 2px);
			background-repeat: no-repeat;
			background-size: cover;
			background-position: 50% 50%;
			overflow: hidden;
			background: rgba(220, 220, 220, 0.2);

			img {
				display: block;
				margin: 0;
				max-height: 100%;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				user-select: none;
			}

		}

		span.dashicons {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 25px;
			width: 25px;
			opacity: .2;
		}
	}
	
	.wpo_unused_image__input:checked + label,
	&.selected label {
		border-color: $wp-blue;
	}

	&:focus-within label {
		box-shadow: 0 0 4px $wp-blue;
	}
	
	a, a.button, a.button:active {
		position: absolute;
		z-index: 2;
		bottom: 13px;
		left: 50%;
		transform: translateX(-50%);
		display: none;
	}

	&:hover {

		a, a.button, a.button:active {
			display: inline-block;
		}

	}

}

#wpo_unused_images, #wpo_unused_images_trash, #wpo_smush_images_grid {
	width: calc(100% + 8px);
    margin-left: -4px;
    margin-right: -4px;
	max-height: 500px;
	overflow-y: auto;
    display: flex;
	flex-wrap: wrap;
	position: relative;

	.wpo-fieldgroup {
		width: 100%;
		margin: 4px;
	}

}

#wpo_unused_images a, #wpo_unused_images_trash a {
	outline: none;
}

.wpo-unused-images__premium-mask,
.wpo-unused-image-sizes__premium-mask {
	position: absolute;
	top: 0; 
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.85));
	display: flex;
	align-items: center;
	justify-content: center;
}

.wpo-unused-image-sizes__premium-mask {
	background: linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.85));
}

a.wpo-unused-images__premium-link {
	background: #FFF;
	display: inline-block;
	padding: 10px;
	border-radius: 3px;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
