.wpo-text__dim {
  .dashicons-info {
    margin-top: 2px;
    margin-right: 5px;
    border-radius: 30px;
    background: #fff;
    color: $red;
  }
}

#page_cache_length_value {
  height: 28px;
  position: relative;
  top: 2px;
}

#wp_optimize_preload_cache_status {
  margin-left: 10px;
  display: inline-block;
  color: #82868B;
  font-size: smaller;
}

.align-left {
    margin: 10px 10px 0px 0px;
    float: left;
}

textarea.cache-settings {
    display: block;
    clear: both;
    min-height: 225px;
    overflow: scroll;
    min-width: 75%;
    margin: 10px 0px;
}

#wp-optimize-purge-cache, 
#wp-optimize-purge-cache ~ span, 
#wp-optimize-purge-cache ~ img {
  vertical-align: middle;
  top: auto;
}

#wp-optimize-nav-tab-wpo_cache-cache-contents .wpo-error {
  font-size: 12px;
}


#wpo_browser_cache_error_message {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 0;
  margin-bottom: 10px;
  
  &:empty {
    display: none;
  }
}

#wp-optimize-nav-tab-wpo_cache-advanced-contents,
#wp-optimize-nav-tab-wpo_cache-preload-contents,
#wp-optimize-nav-tab-wpo_cache-cache-contents {
  label {
    font-weight: 600;
    em {
      font-weight: normal;
    }
  }
}

.wpo-gzip-already-enabled span.dashicons.dashicons-yes,
.wpo-enabled > span.dashicons.dashicons-yes {
  font-size: 40px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  vertical-align: middle;
  color: green;
}
