@keyframes rotate {

	0% {
		transform: rotate(0);
	}

	100% {
		transform: rotate(360deg);
	}

}

/* Common BLOCKS */


/* Buttons */

#wp-optimize-wrap {

	.button-large {
		@media (max-width: $breakpoint-small) {
			display: block;
			width: 100%;
		}
	}

	.red {
		color: $red;
	}

	div[id*="_notice"] div.updated,
	> .notice {
		margin-left: 0;
		margin-right: 0;
	}

	textarea {
		width: 100%;
		border-radius: 5px;
		height: 100px;
		min-height: 100px;
		margin-top: 10px;
		margin-bottom: 10px;
	}
	
}

.notice.notice-error.wpo-warning,
.notice.notice-warning.wpo-warning {
	margin-bottom: 18px;
}

.notice.notice-warning.wpo-warning p {
	padding-left: 36px;
	position: relative;
}

.notice.notice-warning.wpo-warning p > .dashicons {
	position: absolute;
	left: 0;
	font-size: 26px;
	top: 50%;
	transform: translateY(-50%);
	height: 26px;
	color: #FFB900;
}

.button.button-block {
	display: block;
	width: 100%;
	text-align: center;
}

.wpo-refresh-button {
	float: right;
}

.wpo-refresh-button:hover {
	cursor: pointer;
}

.wpo-refresh-button .dashicons {
	text-decoration: none;
	line-height: inherit;
	font-size: inherit;
}

/* Helper classes */

*[class*="wpo-badge"] {
	display: inline-block;
	font-size: .8em;
	text-transform: uppercase;
	background: $wp-lighter-gray;
	padding: 3px 5px;
	line-height: 1;
	border-radius: 3px;
}

.wpo-badge__new {
	background: #DBE3E6;
	color: #00689a;
}

.wpo-first-child {
	margin-top: 0;
}

#save_done, .save-done {
	color: $success;
	font-size: 250%;
}

p.wpo-take-a-backup {
	display: inline-block;
	margin: 0;
	line-height: 1;
	padding-top: 8px;
}

@media (min-width: $breakpoint-small) {
	.button ~ p.wpo-take-a-backup {
		margin-left: 20px;
	}
}

/* Postbox */

#wp-optimize-wrap .nav-tab-wrapper ~ .wp-optimize-nav-tab-contents > .postbox {
	border: none;

	> h3:first-child {
		margin-top: 0;
	}

}

.wpo-p25,
.postbox.wpo-tab-postbox {
	padding: 25px;
}

.wpo-tab-postbox {
	
	> h3:first-child {
		margin-top: 0;
	}
}

/* Field group */

.wpo-fieldgroup {
	background: $wp-lighter-gray;
	border-radius: 8px;
	padding: 20px;

	&:not(:last-child) {
		margin-bottom: 2em;
	}

	> *:first-child {
		margin-top: 0;
	}
	
	> *:last-child {
		margin-bottom: 0;
	}

	&.premium-only {
		position: relative;
	}

	.switch + label {
		font-weight: 600;
	}

	code, pre.code {
		font-size: inherit;
		background: #d8dbdc;
		border-radius: 3px;
	}

	pre.code {
		padding: 10px;
	}

	fieldset {

		label {
			display: block;
			margin-bottom: 6px;
			line-height: 20px;
		}

	}

}

.wpo-fieldgroup__subgroup {
	&:not(:last-of-type) {
		margin-bottom: 20px;
	}
}

/* The switches */
#wp-optimize-wrap {

	.switch {
		position: relative;
		display: inline-block;
		width: 38px;
		height: 18px;
		margin-right: 6px;
		box-sizing: border-box;

		/* Hide default HTML checkbox */
		input {
			opacity: 0;
			width: 0;
			height: 0;
		}

		/* The slider */
		.slider {
			position: absolute;
			cursor: pointer;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: $wp-lighter-gray;
			transition: .2s;

			&::before {
				position: absolute;
				content: "";
				height: 8px;
				width: 8px;
				left: 2px;
				bottom: 2px;
				background-color: $wp-gray-dark;
				border: 1px solid $wp-gray-dark;
				transition: all .2s;
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				height: 4px;
				width: 3px;
				right: 4px;
				top: 4px;
				border-radius: 50%;
				border: 1px solid $wp-secondary-gray;
				box-sizing: content-box
			}

			/* Rounded sliders */
			&.round {
				border-radius: 23px;
				border: 2px solid $wp-gray-dark;
				&::before {
				border-radius: 50%;
				}
			}

		}

		input:checked + .slider {
			background: $wp-blue;
			border-color: $wp-blue;
			&::before {
				background: #FFF;
				border-color: #FFF;
				transform: translateX(20px);
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				height: 6px;
				width: 2px;
				left: 7px;
				top: 4px;
				background: #FFF;
				border: none;
			}

		}
		input:disabled + .slider,
		input:disabled:checked + .slider {
			background: transparentize($wp-gray-dark, 0.5);
			border-color: $wp-gray-dark;
			opacity: 0.3;
		}		

		input:focus + .slider {
			box-shadow: 0 0 0 2px $wp-lighter-gray, 0 0 0 3px $wp-gray-dark;
		}

	}

	.switch-container {
		display: block;
		clear: both;
		label {
			line-height: 1;
			vertical-align: middle;
		}
		& + small {
			margin-left: 49px;
		}
		[data-tooltip] .dashicons {
			transform: translateY(-3px);
		}
	}

	.wpo-fieldgroup > .switch-container:first-child {
		padding-top: 0;
	}

}

label.wpo-label__bold {
	font-weight: 600;
}

/* Help popup */

.wpo-info {
	position: relative;
	display: inline-block;
	@media (max-width:480px) {
		display: block;
	}
}

.wpo-info__content {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 100%;
	left: -25px;
	background: #FFF;
	padding: 20px;
	width: 380px;
	max-width: 380px;
	z-index: 5;
	box-shadow: 0px 11px 25px 0px rgba(0, 0, 0, 0), 0px 11px 25px 3000px rgba(0, 0, 0, 0);
	transition: 0.2s all;

	@media (max-width:480px) {
		width: auto;
	}
}

span.wpo-info__close {
	display: none;
	margin-left: 20px;
	color: #444;
}

.wpo-info.opened {

	span.wpo-info__close {
		display: inline-block;
	}

	.wpo-info__content {
		opacity: 1;
		visibility: visible;
		box-shadow: 0px 11px 25px 0px rgba(0, 0, 0, 0.3), 0px 11px 25px 3000px rgba(0, 0, 0, 0.3);
	}

	a.wpo-info__trigger {
		z-index: 6;
	}
	
}

.wpo-info__content img,
.wpo-info__content iframe {
	max-width: 100%;
}

a.wpo-info__trigger {
	display: inline-block;
	text-decoration: none;
	padding: 10px;
	position: relative;
	background: #FFF;
	margin-left: -10px;
}

a.wpo-info__trigger span.dashicons {
	text-decoration: none;
	vertical-align: middle;
}

.wpo_section.wpo_group.wpo-feature-is-disabled .wpo-fieldgroup:not(.wpo-show) {
    opacity: 0.5;
}

/* Custom Block UI */

.wpo_shade {
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 9979;
	opacity: 1;
	background-color: rgba(255, 255, 255, 0.88);
}

.wpo_shade.hidden {
	display: none;
}

.wpo_shade_inner {
	text-align: center;
	padding-top: 80px;
}

.wpo_shade_inner h4 {
	font-size: 2em;
}

.wpo_shade_inner .dashicons.dashicons-update-alt {
	font-size: 50px;
	height: 50px;
	width: 50px;
}

.wpo-rotate {
	animation: rotate 1s linear infinite;
	transform-origin: 50% 50%;
}

.wpo-info.highlight-dashicons span.dashicons {
	color: green;
	font-weight: bold;
}