.power-tweaks--list {
	@media (min-width: 780px) {
		display: flex;
	    flex-direction: row;
		flex-wrap: wrap;
	}
	
	.wpo-power-tweak {
		width: 100%;
		@media (min-width: 780px) {
			width: calc(50% / 2 - 1em);
			margin-right: 1em;
		}
		@media (min-width: 1080px) {
			width: calc(100% / 3 - 1em);
			margin-right: 1em;
		}
		background: #f2f4f5;
		border-radius: 1em;
		padding: 1.5em;
		margin-bottom: 1em;
		box-sizing: border-box;

		h4 {
			margin-top: 0;
		}
		
		.description {
			margin-bottom: 1em;
		}

		.details {
			padding: 1em;
			background: #FFF;
			box-shadow: 0 2px 3px rgba(0,0,0,0.1);
			margin-top: 1em;
			margin-bottom: 1em;
		}
		
		.actions {
			margin-top: 1em;
			margin-bottom: 1em;
		}

		.last-run, .last-updated {
			margin-top: 1em;
			font-size: 0.8em;
			color: #a9a9a9;
		}

		.tweak-is-running {
			display: none;
		}

		&.running {
			position: relative;
			.tweak-is-running {
				display: flex;
				align-items: center;
				justify-content: center;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				background: rgb(242, 244, 245, 0.9);
				z-index: 1;
				border-radius: 1em;
				font-size: 1.7em;
				.spinner {
					visibility: visible;
				}
			}
		}
	}

	a.show-faqs span.dashicons {
		font-size: inherit;
		height: auto;
		width: auto;
		line-height: inherit;
		text-decoration: none;
		margin-left: 4px;
	}
	
}
