#wp-optimize-wrap {

	.filename {
		display: inline-block;
		padding: 6px 0;
	}

	.no-files-yet .filename {
		font-style: italic;
	}

	.processed {
		margin: 0;
	}

	.processed .wpo_min_log {
		padding: 8px;
		background: white;
		border: 1px solid #CDCDCD;
		border-radius: 4px;
		line-height: 1.5em;
		ul {
			overflow-x: auto;
			li {
				white-space: nowrap;
				&.failed .wpo_min_file_url {
					color: #dc3232;
				}
				&.disable-list-item {
					opacity: 0.5;
				}
			}
		}
		h5 {
			margin-top: 0;
			margin-bottom: 8px;
			border-bottom: 1px solid #cdcdcd;
			padding-bottom: 6px;
		}
		.wpo_min_file_debug {
			color: #9c9c9c;
			display: inline-block;
			&::before {
				content: '';
				display: inline-block;
				margin: 0 10px;
			}
		}
	}

	.show-if-enabled {
		display: none;
	}

	.is-enabled .show-if-enabled {
		display: block;
	}

	.wpo-page:not(.is-enabled) .wpo-minify-features {
		display: none;
	}

	.wpo-page[data-whichpage="wpo_minify"]:not(.is-enabled) .nav-tab-wrapper > a:not([data-tab="status"]):not([role="toggle-menu"]){
		display: none;
	}
	
	.wpo-minify-features {
		padding-top: 1em;
		margin-top: 1em;
		border-top: 1px solid #bbb;
	}

	.wpo-minify-features .wpo-fieldgroup__subgroup {
		margin-bottom: 1em;
	}

	.wpo-fieldgroup__subgroup.wpo_min_enable_minify + .wpo-fieldgroup__subgroup {
		display: none;
	}
	
	.wpo-fieldgroup__subgroup.wpo_min_enable_minify.enabled + .wpo-fieldgroup__subgroup {
		display: block;
	}

	.wpo-fieldgroup__subgroup.wpo_min_enable_minify:not(.enabled) {
		margin-bottom: 0;
	}

	.defer-js-settings {
		padding-left: 1em;
		border-left: 2px solid #E2E2E2;
		margin-left: 0.5em;
	}

	/* 
		Asset preload table
	*/
	.asset-preload-list {
		margin-bottom: 1em;
		td.asset-href {
			width: 55%;
		}

		td.asset-crossorigin {
			width: 10%;
		}
	
		tr.editing {
			display: none;
		}

		tr.form {
			box-shadow: 0 2px 4px #00000021;
		}

		tr.form td {
			background: #FFF;
			border: none;
			padding: 10px;
		}
		
		tr.editing + tr.form td:last-child {
			border-radius: 0 5px 5px 0;
		}

		tr.editing + tr.form td:first-child {
			position: relative;
			&::before {
				content: '';
				width: 10px;
				height: 10px;
				background: #FFF;
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				transform: translate(10px, -5px) rotate(45deg);
			}
		}
		tbody + tr.form {
			td {
				padding-top: 37px;
				position: relative;
				&[data-label]::before {
					content: attr(data-label);
					position: absolute;
					top: 1em;
					left: 10px;
					text-transform: uppercase;
					color: #D0D2D4;
				}
			}
		}

		input#preload_href {
			width: 100%;
		}
		
		select#preload_type {
			width: 100%;
		}
		
		td.asset-preload-form--actions,
		td.asset-edit {
			text-align: right;
			button {
				display: inline-block;
				&.wpo-asset--edit {
					&::after {
						content: '|';
						padding-left: 6px;
						padding-right: 4px;
					}
				}
			}
		}
	}
}
